import React from 'react'
import { graphql } from 'gatsby'
import { MDXRenderer } from 'gatsby-plugin-mdx'

import Layout from '../components/Layout'
import SEO from '../components/SEO'
import TableOfContents from '../components/TableOfContents'
import PostPreview from '../components/PostPreview'
import Adsense from '../components/Adsense'

export const query = graphql`
  query PostBySlug($slug: String!) {
    post: mdx(slug: { eq: $slug }) {
      body
      tableOfContents
      frontmatter {
        title
        date(formatString: "YYYY-MM-DD")
      }
    }
    relatedPosts: allMdx(
      filter: {
        fileAbsolutePath: { regex: "/posts/" }
        frontmatter: { related_to: { in: [$slug] } }
      }
    ) {
      edges {
        node {
          slug
          excerpt
          frontmatter {
            title
            seo_title
            thumbnail {
              childImageSharp {
                fluid {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    }
  }
`

export default function PostPage({ data }) {
  const { post, relatedPosts } = data

  return (
    <Layout>
      <SEO title={post.frontmatter.seo_title || post.frontmatter.title} />
      <h1>{post.frontmatter.title}</h1>
      <h2>{post.frontmatter.date}</h2>
      <TableOfContents {...post.tableOfContents} />
      <MDXRenderer>{post.body}</MDXRenderer>
      <Adsense />
      {relatedPosts.edges.length > 0 && (
        <div>
          <h3>Муська рекомендует</h3>
          <div className="flex flex-wrap">
            {relatedPosts.edges.map(edge => (
              <PostPreview key={edge.node.slug} post={edge.node} />
            ))}
          </div>
        </div>
      )}
    </Layout>
  )
}
