import React, { useState } from 'react'
import Adsense from './Adsense'

function TableOfContentsList({ items, level = 1 }) {
  return (
    <ul>
      {items.map((item, idx) => (
        <li key={item.title}>
          <a href={item.url}>
            <span className="text-teal-600">
              {idx + 1}. {item.title}
            </span>
            {item.items && (
              <TableOfContentsList items={item.items} level={level + 1} />
            )}
          </a>
        </li>
      ))}
    </ul>
  )
}

function TableOfContents({ items }) {
  const [expanded, setExpanded] = useState(true)

  function handleToggle() {
    setExpanded(!expanded)
  }

  if (!items) {
    return null
  }

  return (
    <div className="flex">
      <div className="bg-gray-100 p-1 border border-gray-700">
        <div className="text-center">
          <span className="text-black">Содержание</span>
          <span>
            &nbsp;[
            <a onClick={handleToggle} className="text-teal-600 cursor-pointer">
              {expanded ? 'убрать' : 'покзаать'}
            </a>
            ]&nbsp;
          </span>
        </div>
        {expanded && <TableOfContentsList items={items} />}
      </div>
      <div>
        <Adsense />
      </div>
    </div>
  )
}

export default TableOfContents
